
Template.__checkName("Mongol");
Template["Mongol"] = new Template("Template.Mongol", (function() {
  var view = this;
  return HTML.DIV({
    id: "Mongol",
    "class": function() {
      return [ Spacebars.mustache(view.lookup("active")), " MeteorToys MeteorToys_hide_Mongol" ];
    },
    oncontextmenu: "return false;"
  }, "\n\n		", Blaze.If(function() {
    return Spacebars.call(view.lookup("MeteorToys_Pro"));
  }, function() {
    return [ "\n		\n			", Spacebars.include(view.lookupTemplate("Mongol_header_pro")), "\n			", Spacebars.include(view.lookupTemplate("Mongol_account")), "\n\n			", Blaze.Each(function() {
      return Spacebars.call(view.lookup("Mongol_collections"));
    }, function() {
      return [ "\n				", Spacebars.include(view.lookupTemplate("Mongol_collection")), "\n			" ];
    }, function() {
      return [ "\n				", Spacebars.include(view.lookupTemplate("Mongol_collection_notFound")), "\n			" ];
    }), "\n\n			", Blaze.If(function() {
      return Spacebars.call(view.lookup("Mongol_isExtended"));
    }, function() {
      return [ "\n				", Spacebars.include(view.lookupTemplate("Mongol_extensions")), "\n			" ];
    }), "\n			", Spacebars.include(view.lookupTemplate("Mongol_trash")), "\n\n		" ];
  }, function() {
    return [ "\n\n			", Spacebars.include(view.lookupTemplate("Mongol_header")), "\n			", Spacebars.include(view.lookupTemplate("Mongol_account")), "\n			", Blaze.Each(function() {
      return Spacebars.call(view.lookup("Mongol_collections"));
    }, function() {
      return [ "\n				", Spacebars.include(view.lookupTemplate("Mongol_collection")), "\n			" ];
    }, function() {
      return [ "\n				", Spacebars.include(view.lookupTemplate("Mongol_collection_notFound")), "\n			" ];
    }), "\n\n			", Blaze.If(function() {
      return Spacebars.call(view.lookup("Mongol_isExtended"));
    }, function() {
      return [ "\n				", Spacebars.include(view.lookupTemplate("Mongol_extensions")), "\n			" ];
    }), "\n\n		" ];
  }), "\n\n	");
}));
