
Template.__checkName("Mongol_docInsert");
Template["Mongol_docInsert"] = new Template("Template.Mongol_docInsert", (function() {
  var view = this;
  return [ HTML.Raw('<div class="Mongol_docMenu">\n		<div class="MeteorToys_action Mongol_docMenu_insert" style="float: right">Insert</div>\n		&nbsp;Create the First Document\n	</div>\n\n	'), HTML.DIV({
    "class": "Mongol_documentViewer ",
    id: function() {
      return [ "Mongol_", Spacebars.mustache(view.lookup(".")), "_newEntry" ];
    },
    tabindex: "-1",
    contenteditable: "true"
  }, "	\n", HTML.Raw("<pre>{\n\n}</pre>"), "\n\n	") ];
}));
