
Template.__checkName("Mongol_account");
Template["Mongol_account"] = new Template("Template.Mongol_account", (function() {
  var view = this;
  return Blaze._TemplateWith(function() {
    return {
      name: Spacebars.call("account_618")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("Mongol_Component"), function() {
      return [ "\n\n			", HTML.Comment(" Display sign in status "), "\n			", Blaze.If(function() {
        return Spacebars.call(view.lookup("currentUser"));
      }, function() {
        return [ "\n				", HTML.DIV({
          "class": "Mongol_account_active"
        }), "\n			" ];
      }, function() {
        return [ "\n				", HTML.DIV({
          "class": "Mongol_account_inactive"
        }), "\n			" ];
      }), "\n\n			", HTML.Comment(" Row Name "), "\n			", HTML.DIV({
        "class": "Mongol_icon Mongol_icon_user"
      }), "\n			Account\n     \n        ", HTML.DIV({
        "class": "Mongol_contentView"
      }, "\n\n			", HTML.Comment(" Document Viewer "), "\n			", Blaze.If(function() {
        return Spacebars.call(view.lookup("currentUser"));
      }, function() {
        return [ "\n				", Spacebars.include(view.lookupTemplate("Mongol_accountViewer")), "\n			" ];
      }, function() {
        return [ "\n				", Spacebars.include(view.lookupTemplate("Mongol_accountViewer_notSignedIn")), "\n			" ];
      }), "\n\n		"), "\n\n	" ];
    });
  });
}));
